ype');\n  font-weight: 800;\n}\n\n// font family\n$sofia: 'Sofia Pro';\n\n// font size\n$text-h1: 61px;\n$text-h2: 48px;\n$text-h3: 39px;\n$text-h4: 32px;\n$text-h5: 25px;\n$text-title: 20px;\n$text-subtitle: 18px;\n$text-body: 16px;\n$text-small: 14px;\n$text-smaller: 12px;\n$text-tiny: 10px;\n\n// font weight\n$font-black: 800;\n$font-bold: 700;\n$font-semibold: 600;\n$font-medium: 500;\n$font-regular: 400;\n$font-light: 300;\n","@import './colors';\n\nbody {\n  // === Light theme ===\n  --body-bg: #{$dark-background};\n  --primary-text: #{$white};\n\n  // === Dark theme ===\n  &[data-theme='dark'] {\n    --body-bg: #{$dark-background};\n    --primary-text: #{$white};\n  }\n}\n","// Font Styles\n@import '../../../assets/styles/fonts';\n\n// Color Vars\n@import '../../../assets/styles/theme';\n@import '../../../assets/styles/colors';\n\n// Mixins\n@import '../../../assets/styles/mixins';\n\nbody {\n  overflow: overlay;\n  background: var(--body-bg);\n  color: var(--primary-text);\n  font-family: $sofia;\n  font-size: $text-body;\n  font-weight: $font-regular;\n  line-height: 149%;\n  margin: 0;\n  padding: 0;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  // & * {\n  //   transition: all 0.3s !important;\n  // }\n}\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  font-family: $sofia;\n  text-decoration: unset;\n  color: var(--primary-text);\n  transition: 0.3s;\n}\n\nli {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n}\n\nh1 {\n  font-size: $text-h1;\n}\nh2 {\n  font-size: $text-h2;\n}\nh3 {\n  font-size: $text-h3;\n}\nh4 {\n  font-size: $text-h4;\n}\nh5 {\n  font-size: $text-h5;\n}\n\nhr {\n  border: 0px;\n  height: 1px;\n  width: 100%;\n  background: $xk-80;\n}\n\n/*\n * Scrollbar\n*/\n::-webkit-scrollbar {\n  width: 12px;\n  height: 12px;\n}\n\n::-webkit-scrollbar-track {\n  background-color: rgba($grey-100, 0.1);\n  border-radius: 8px;\n  margin: 5px;\n  background-clip: padding-box;\n  border: 2px