\n  background: $xk-80;\n}\n\n/*\n * Scrollbar\n*/\n::-webkit-scrollbar {\n  width: 12px;\n  height: 12px;\n}\n\n::-webkit-scrollbar-track {\n  background-color: rgba($grey-100, 0.1);\n  border-radius: 8px;\n  margin: 5px;\n  background-clip: padding-box;\n  border: 2px solid rgba(0, 0, 0, 0);\n}\n\n::-webkit-scrollbar-corner {\n  background-color: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 8px;\n  border: 2px solid rgba(0, 0, 0, 0);\n  background-color: rgba($grey-80, 0.9);\n  background-clip: padding-box;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: rgba($grey-60, 0.9);\n}\n\n.container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n\n    > * {\n        width: 100%;\n        max-width: 605px;\n\n        @include mediumMedia {\n            max-width: 480px;\n        }\n\n        @include smallMedia {\n            max-width: 400px;\n        }\n    }\n\n    .week_day {\n        display: flex;\n        align-items: flex-end;\n        gap: 12px;\n        margin-top: 40px;\n\n        :first-child {\n            font-size: $text-title;\n            font-weight: $font-bold;\n            line-height: 24px;\n        }\n\n        :last-child {\n            font-size: $text-smaller;\n            line-height: 14.4px;\n            color: $grey-40;\n            margin-bottom: 2px;\n        }\n    }\n\n    .countdown_container{\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n        background-color: $grey-100;\n        border-radius: 12px;\n        padding: 25px;\n        margin-top: 13px;\n        gap: 12px;\n\n        svg {\n            height: 150px;\n            width: 150px;\n        }\n\n        span {\n            margin-top: 32px;\n            line-height: 19.2px;\n        }\n\n        .time_container {\n            display: flex;\n            gap: 12px;\n\n            h4 {\n                line-height: 100%;\n                color: $forward-red;