AA,CACA,iBAAA,CACA,yBAAA,CACA,yBAAA","sourcesContent":["/* -------------------------------- Sofia Pro ------------------------------- */\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ UltraLight\\ Az.otf') format('opentype');\n  font-weight: 100;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ ExtraLight\\ Az.otf') format('opentype');\n  font-weight: 200;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Light\\ Az.otf') format('opentype');\n  font-weight: 300;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Regular\\ Az.otf') format('opentype');\n  font-weight: 400;\n}\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Medium\\ Az.otf') format('opentype');\n  font-weight: 500;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Bold\\ Az.otf') format('opentype');\n  font-weight: 600;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Bold\\ Az.otf') format('opentype');\n  font-weight: 700;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Black\\ Az.otf') format('opentype');\n  font-weight: 800;\n}\n\n// font family\n$sofia: 'Sofia Pro';\n\n// font size\n$text-h1: 61px;\n$text-h2: 48px;\n$text-h3: 39px;\n$text-h4: 32px;\n$text-h5: 25px;\n$text-title: 20px;\n$text-subtitle: 18px;\n$text-body: 16px;\n$text-small: 14px;\n$text-smaller: 12px;\n$text-tiny: 10px;\n\n// font weight\n$font-black: 800;\n$font-bold: 700;\n$font-semibold: 600;\n$font-medium: 500;\n$font-regular: 400;\n$font-light: 300;\n","@import './colors';\n\nbody {\n  // === Light theme ===\n  --body-bg: #{$dark-background};\n  --primary-text: #{$white};\n\n  // === Dark theme ===\n  &[data-theme='dark'] {\n    --body-bg: #{$dark-background};\n    --primary-text: #{$white};\n  }\n}\n","// Font Styles\n@import '../../../assets/styles/fonts';\n\n// Color Vars\n@import '../../../assets/styles/theme';\n@import '../../../assets/styles/colors';\n\n// Mixins\n@import '../../../assets/styles/mixins';\n\nbody {\n  overflow: overlay;\n  background: var(--body-bg);\n  color: var(--primary-text);\n  font-family: $sofia;\n  font-size: $text-body;\n  font-weight: $font-regular;\n  line-height: 149%;\n  margin: 0;\n  padding: 0;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  // & * {\n  //   transition: all 0.3s !important;\n  // }\n}\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  font-family: $sofia;\n  text-decoration: unset;\n  color: var(--primary-text);\n  transition: 0.3s;\n}\n\nli {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n}\n\nh1 {\n  font-size: $text-h1;\n}\nh2 {\n  font-size: $text-h2;\n}\nh3 {\n  font-size: $text-h3;\n}\nh4 {\n  font-size: $text-h4;\n}\nh5 {\n  font-size: $text-h5;\n}\n\nhr {\n  border: 0px;\n  height: 1px;\n  width: 100%;\n  background: $xk-80;\n}\n\n/*\n * Scrollbar\n*/\n::-webkit-scrollbar {\n  width: 12px;\n  height: 12px;\n}\n\n::-webkit-scrollbar-track {\n  background-color: rgba($grey-100, 0.1);\n  border-radius: 8px;\n  margin: 5px;\n  background-clip: padding-box;\n  border: 2px solid rgba(0, 0, 0, 0);\n}\n\n::-webkit-scrollbar-corner {\n  background-color: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 8px;\n  border: 2px solid rgba(0, 0, 0, 0);\n  background-color: rgba($grey-80, 0.9);\n  background-clip: padding-box;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: rgba($grey-60, 0.9);\n}\n\n.container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n\n    > * {\n        width: 100%;\n        max-width: 605px;\n\n        @include mediumMedia {\n            max-width: 480px;\n        }\n\n        @include smallMedia {\n            max-width: 400px;\n        }\n    }\n\n    .week_day {\n        display: flex;\n        align-items: flex-end;\n        gap: 12px;\n        margin-top: 40px;\n\n        :first-child {\n            font-size: $text-title;\n            font-weight: $font-bold;\n            line-height: 24px;\n        }\n\n        :last-child {\n            font-size: $text-smaller;\n            line-height: 14.4px;\n            color: $grey-40;\n            margin-bottom: 2px;\n        }\n    }\n\n    .head_description {\n        margin-top: 8px;\n        font-size: $text-smaller;\n        line-height: 14.4px;\n        color: $grey-40;\n    }\n\n    .tab_container {\n        position: relative;\n        display: flex;\n        align-items: center;\n        height: 62px;\n        margin-top: 12px;\n\n        \n        &::before {\n            content: '';\n            position: absolute;\n            justify-self: center;\n            // top: 0;\n            left: -50vw;\n            right: -50vw;\n            background-color: $grey-100;\n            height: 100%;\n            z-index: -1;\n        }\n    }\n\n    .tab {\n        position: absolute;\n        width: 100%;\n        display: flex;\n        gap: 11px;\n        overflow-x: scroll;\n        overflow-y: hidden;\n        height: 100%;\n        padding-top: 12px;\n        align-items: center;\n\n        .position {\n            display: flex;\n            gap: 5px;\n            padding-right: 11px;\n            font-weight: $font-bold;\n            border-right: solid 1.5px $grey-80;\n            cursor: pointer;\n\n            &:last-child {\n                border-right: unset;\n            }\n\n            span {\n                color: $grey-40;\n            }\n\n            &.selected span {\n                color: $forward-red;\n            }\n        }\n    }\n\n    .card {\n        background-color: $grey-100;\n        padding: 16px;\n        margin-top: 24px;\n        border-radius: 8px;\n        display: flex;\n        flex-direction: column;\n\n        &.game_on {\n            align-items: center;\n            text-align: center;\n        }\n\n        .congratulations {\n            font-size: $text-title;\n            line-height: $text-title;\n            font-weight: $font-bold;\n            background: $gradient-gold;\n            background-clip: text;\n            -webkit-background-clip: text;\n            color: transparent;\n        }\n\n        .try_agian {\n            font-size: $text-title;\n            line-height: $text-title;\n            font-weight: $font-bold;\n        }\n\n        :nth-child(2):not(:last-child) {\n            margin-top: 24px;\n            font-size: $text-smaller;\n            line-height: 14.4px;\n            color: $grey-60;\n        }\n\n        :nth-child(3):not(:last-child) {\n            margin-top: 8px;\n            line-height: 19.2px;\n        }\n\n        :last-child {\n            margin-top: 24px;\n            color: $grey-40;\n            font-size: $text-small;\n            line-height: 20.86px;\n        }\n    }\n\n    .play {\n        background-color: $forward-red;\n        padding: 11px 0;\n        border: none;\n        border-radius: 8px;\n        font-weight: $font-bold;\n        font-size: $text-subtitle;\n        line-height: 26.82px;\n        margin-top: 40px;\n        cursor: pointer;\n\n        &:disabled {\n            background-color: $grey-80;\n            color: $grey-60;\n        }\n    }\n\n    .buttons_container {\n        margin-top: 32px;\n        display: flex;\n        justify-content: center;\n        gap: 24px;\n\n        a {\n            font-size: $text-small;\n            font-weight: $font-light;\n            line-height: 16.8px;\n            text-align: center;\n            text-decoration: underline;\n            text-underline-off