ro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Regular\\ Az.otf') format('opentype');\n  font-weight: 400;\n}\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Medium\\ Az.otf') format('opentype');\n  font-weight: 500;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Bold\\ Az.otf') format('opentype');\n  font-weight: 600;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Bold\\ Az.otf') format('opentype');\n  font-weight: 700;\n}\n\n@font-face {\n  font-family: 'Sofia Pro';\n  src: local('Sofia Pro'),\n    url('/src/assets/fonts/Sofia\\ Pro\\ Black\\ Az.otf') format('opentype');\n  font-weight: 800;\n}\n\n// font family\n$sofia: 'Sofia Pro';\n\n// font size\n$text-h1: 61px;\n$text-h2: 48px;\n$text-h3: 39px;\n$text-h4: 32px;\n$text-h5: 25px;\n$text-title: 20px;\n$text-subtitle: 18px;\n$text-body: 16px;\n$text-small: 14px;\n$text-smaller: 12px;\n$text-tiny: 10px;\n\n// font weight\n$font-black: 800;\n$font-bold: 700;\n$font-semibold: 600;\n$font-medium: 500;\n$font-regular: 400;\n$font-light: 300;\n","@import './colors';\n\nbody {\n  // === Light theme ===\n  --body-bg: #{$dark-background};\n  --primary-text: #{$white};\n\n  // === Dark theme ===\n  &[data-theme='dark'] {\n    --body-bg: #{$dark-background};\n    --primary-text: #{$white};\n  }\n}\n","// Font Styles\n@import '../../assets/styles/fonts';\n\n// Color Vars\n@import '../../assets/styles/theme';\n@import '../../assets/styles/colors';\n\n// Mixins\n@import '../../assets/styles/mixins';\n\nbody {\n  overflow: overlay;\n  background: var(--body-bg);\n  color: var(--primary-text);\n  font-family: $sofia;\n  font-size: $text-body;\n  font-weight: $font-regular;\n  line-height: 149%;\n  margin: 0;\n  padding: 0;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  // & * {\n  //   transition: all 0.3s !important;\n  // }\n}\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  font-family: $sofia;\n  text-decoration: unset;\n  color: var(--primary-text);\n  transition: 0.3s;\n}\n\nli {\n  list-style: none;\n}\n\na {\n  text-decoration: none;\n}\n\nh1 {\n  font-size: $text-h1;\n}\nh2 {\n  font-size: $text-h2;\n}\nh3 {\n  font-size: $text-h3;\n}\nh4 {\n  font-size: $text-h4;\n}\nh5 {\n  font-size: $text-h5;\n}\n\nhr {\n  border: 0px;\n  height: 1px;\n  width: 100%;\n  background: $xk-80;\n}\n\n/*\n * Scrollbar\n*/\n::-webkit-scrollbar {\n  width: 12px;\n  height: 12px;\n}\n\n::-webkit-scrollbar-track {\n  background-color: rgba($grey-100, 0.1);\n  border-radius: 8px;\n  margin: 5px;\n  background-clip: padding-box;\n  border: 2px solid rgba(0, 0, 0, 0);\n}\n\n::-webkit-scrollbar-corner {\n  background-color: transparent;\n}\n\n::-webkit-scrollbar-thumb {\n  border-radius: 8px;\n  border: 2px solid rgba(0, 0, 0, 0);\n  background-color: rgba($grey-80, 0.9);\n  background-clip: padding-box;\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background-color: rgba($grey-60, 0.9);\n}\n\n.container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    width: 100%;\n    padding: 0 30px;\n\n    > * {\n        width: 100%;\n        max-width: 605px;\n\n        @include mediumMedia {\n            max-width: 480px;\n        }\n\n        @include smallMedia {\n            max-width: 400px;\n        }\n    }\n\n    .body_container {\n        margin: 40px 0;\n        display: flex;\n        flex-direction: column;\n        gap: 24px;\n\n        .title {\n            font-size: $text-title;\n            font-weight: $font-bold;\n            line-height: 24px;\n        }\n\n        .description {\n            font-size: $text-title;\n            line-height: 14.4px;\n            font-size: $text-smaller;\n            color: $grey-40;\n            margin-top: 8px;\n        }\n\n        .title_container {\n            display: flex;\n            flex-direction: column;\n            gap: 8px;\n            \n            > * {\n                display: flex;\n                align-items: flex-end;\n                gap: 8px;\n\n                &:nth-child(2) {\n                    justify-content: space-between;\n                }\n            }\n        }\n\n        .week {\n            font-size: $text-title;\n            line-height: 24px;\n            font-weight: $font-bold;\n        }\n        .day {\n            font-size: $text-smaller;\n            line-height: 14.4px;\n            color: $grey-40;\n        }\n\n        .game_name {\n            font-size: $text-title;\n            line-height: 24px;\n            font-weight: $font-bold;\n            color: $forward-red;\n        }\n\n        .counter_container {\n            display: flex;\n            flex-direction: column;\n            align-items: flex-end;\n\n            > span {\n                font-size: $text-small;\n                line-height: 16.8px;\n            }\n\n            > div {\n                display: flex;\n\n                .number {\n                    text-align: right;\n                }\n\n                .number:nth-child(3) {\n                    width: 18px;\n                    text-align: right;\n                }\n\n                .number:last-child {\n                    width: 27px;\n                }\n            }\n        }\n\n        .attempts_container {\n            display: flex;\n            flex-direction: column;\n            gap: 10px;\n            align-items: center;\n        }\n\n        .blocks_container {\n            display: flex;\n            gap: 10px;\n        }\n\n        .block {\n            width: 52.8px;\n            height: 56px;\n            background: linear-gradient(180deg, #30323C 0%, #212227 100%);\n            border-radius: 8px;\n            padding: 4px 16.9px;\n\n            h4 {\n                font-weight: $font-bold;\n                line-height: 47.68px;\n            }\n\n            &.green {\n                background: linear-gradient(180deg, #259968 0%, #11C9BE 0.01%, #074541 100%);\n            }\n\n            &.yellow {\n                background: linear-gradient(180deg, #E9B433 0%, #78590C 100%);\n            }\n\n            &.grey {\n                background: $grey-80;\n\n                h4 {\n                    color: $grey-60;\n                }\n            }\n\n            &.shake {\n                animation: shake 0.5s;\n                animation-iteration-count: 1;\n            }\n            \n            @keyframes shake {\n                0% {\n                    transform: translate(1px, 1px) rotate(0deg);\n                }\n                10% {\n                    transform: translate(-1px, -2px) rotate(-3deg);\n                }\n                20% {\n                    transform: translate(-3px, 0px) rotate(3deg);\n                }\n                30% {\n                    transform: translate(3px, 2px) rotate(0deg);\n                }\n                40% {\n                    transform: translate(1px, -1px) rotate(3deg);\n                }\n                50% {\n                    transform: translate(-1px, 2px) rotate(-3deg);\n                }\n                60% {\n                    transform: translate(-3px, 1px) rotate(0deg);\n                }\n                70% {\n                    transform: translate(3px, 1px) rotate(-3deg);\n                }\n                80% {\n                    transform: translate(-1px, -1px) rotate(3deg);\n                }\n                90% {\n                    transform: translate(1px, 2px) rotate(0deg);\n                }\n                100% {\n                    transform: translate(1px, -2px) rotate(-3deg);\n                }\n            }\n        }\n    }\n\n    \n}\n\n.modal_container {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    text-align: center;\n    gap: 32px;\n\n    .modal_title {\n        font-weight: $font-bold;\n        font-size: $text-h4;\n        line-height: 149%;\n\n        &.gold {\n            background: $gradient-gold;\n            background-clip: text;\n            -webkit-background-clip: text;\n            color: transparent;\n        }\n    }\n\n    .modal_time_used {\n        display: flex;\n        line-height: 23.84px;\n        gap: 68px;\n    }\n\n    .modal_description {\n        font-size: $text-small;\n        line-height: 20.86px;\n        color: $grey-40;\n    }\n\n    a {\n        line-height: 19.2px;\n        text-decoration: underline;\n        text-underline-offset: 2px;\n    }\n}","$black: black;\n$white: white;\n\n//Brand Color\n$gradient-forward-red: linear-gradient(\n  85.7deg,\n  #aa448f 0%,\n  #ff1665 54.13%,\n  #fa6c81 101.89%\n);\n$forward-red: #fb3a70;\n$xiketic: #141429;\n\n//base\n$base: #121220;\n$white-transparent: rgba(255, 255, 255, 0.8);\n$dark-background: #080811;\n$light-background: #f3f3fa;\n$xk-90: #1d1d3d;\n$xk-80: #2a2a56;\n$xk-70: #3d3d6e;\n$xk-60: #4b4b9a;\n$xk-40: #6565b4;\n\n$xk-30: #e1e1f0;\n$xk-20: #e5e5f5;\n$xk-10: #eeeef7;\n\n$dark-grey: #191929;\n$light-grey: #ededfa;\n$grey-xk: #5e5e89;\n$icon-grey: #3d4259;\n$subtle-text: #8585b9;\n\n$grey-110: #191919;\n$grey-100: #1e1f25;\n$grey-90: #2f3241;\n$grey-85: #24252c;\n$grey-80: #34384c;\n$grey-60: #5d6588;\n$grey-40: #a5adcf;\n$grey-20: #e5e6ed;\n$grey-10: #fafafa;\n$grey-5: #f9f9f9;\n$grey-0: #ffffff;\n\n//Accent\n$red-primary: #fa2256;\n$green-primary: #11cabe;\n$green-light: #30e0a1;\n$purple: #bd47fb;\n$orange: #f27f16;\n$sunlight: #ffaf37;\n$accent: rgba(63, 140, 255, 1);\n\n$gradient-rainbow: linear-gradient(\n  90deg,\n  #ff9f9f 0%,\n  #fc5792 32.81%,\n  #ffc657 70.31%,\n  #95fe10 100%\n);\n$gradient-gold: linear-gradient(90.04deg, #f2c94c 3.68%, #ebff00 54.19%);\n$gradient-green: linear-gradient(101.25deg, #aeff2a 6.67%, #2affa4 97.29%);\n\n//Border\n$gradient-sliver-border-01: linear-gradient(\