ient(\n  224.57deg,\n  #d8a72b 2.02%,\n  #ff922e 23.14%,\n  #f3b66f 38.26%,\n  #ffcf97 54.63%,\n  #c76b00 86.33%,\n  #f48f45 117.73%\n);\n\n//Forwordle\n$correct-tile: linear-gradient(\n  180deg, \n  #259968 0%,\n  #11c9be 0.01%,\n  #074541 100%\n);\n$almost-correct-tile: linear-gradient(\n  180deg, \n  #e9b433 0%, \n  #78590c 100%\n);\n","// media\n@mixin superLargeMedia { //special for trade page\n  @media screen and (max-width: 1400px) {\n    @content;\n  }\n}\n\n@mixin extraLargeMedia {\n  @media screen and (max-width: 1280px) {\n    @content;\n  }\n}\n\n@mixin largeMedia {\n  @media screen and (max-width: 1024px) {\n    @content;\n  }\n}\n\n@mixin mediumMedia {\n  @media screen and (max-width: 768px) {\n    @content;\n  }\n}\n\n@mixin smallMedia {\n  @media screen and (max-width: 500px) {\n    @content;\n  }\n}\n\n@mixin extraSmallMedia {\n  @media screen and (max-width: 320px) {\n    @content;\n  }\n}\n\n@mixin gradientBorder {\n  &::before {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    border: 4px double transparent;\n    border-radius: inherit;\n    @content;\n    -webkit-mask: linear-gradient($white 0 0) padding-box,\n      linear-gradient($white 0 0);\n    mask: linear-gradient($white 0 0) padding-box, linear-gradient($white 0 0);\n    -webkit-mask-composite: destination-out;\n    mask-composite: exclude;\n  }\n}\n"],"sourceRoot":""}]);
