ax-width: 400px;\n        }\n    }\n\n    .linked_container {\n        background-color: $grey-100;\n        padding: 14px;\n        border-radius: 8px;\n        gap: 10px;\n\n        span {\n            font-size: $text-smaller;\n            line-height: 17.88px;\n        }\n\n        .linked_account {\n            background-color: $grey-80;\n            border-radius: 8px;\n            display: flex;\n            gap: 10px;\n            padding: 12px;\n            position: relative;\n            align-items: center;\n\n            svg {\n                &:first-child {\n                    width: 24px;\n                    height: 24px;\n                }\n\n                &:last-child {\n                    position: absolute;\n                    right: 12px;\n                }\n            }\n        }\n    }\n\n    .input_container {\n        gap: 8px;\n\n        span {            \n            line-height: 19.2px;\n        }\n    }\n\n    .captcha_img {\n        width: 100%;\n        background-color: white;\n        border: solid 1px white;\n\n        > div {\n            display: flex;\n            justify-content: center;\n        }\n\n        canvas {\n            height: 60px;\n        }\n    }\n\n    .register_button {\n        display: flex;\n        flex-direction: row;\n        justify-content: center;\n        background-color: $forward-red;\n        border: none;\n        border-radius: 8px;\n        font-size: $text-subtitle;\n        line-height: 26.82px;\n        padding: 10px 0;\n\n        &:disabled {\n            color: $grey-60;\n            background: $grey-80;\n        }\n    }\n\n}","$black: black;\n$white: white;\n\n//Brand Color\n$gradient-forward-red: linear-gradient(\n  85.7deg,\n  #aa448f 0%,\n  #ff1665 54.13%,\n  #fa6c81 101.89%\n);\n$forward-red: #fb3a70;\n$xiketic: #141429;\n\n//base\n$base: #121220;\n$white-transparent: rgba(255, 255, 255, 0.8);\n$dark-background: #080811;\n$light-background: #f3f3fa;\n$xk-90: #1d1d3d;\n$xk-8